@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 4413px;
  background: linear-gradient(
    45deg,
    rgba(60, 74, 84, 1) 0%,
    rgba(0, 139, 255, 1) 25%,
    rgba(245, 255, 0, 1) 50%,
    rgba(255, 0, 0, 1) 75%,
    rgba(235, 0, 255, 1) 95%
  );
  background-size: cover;
  z-index: -10;
  -webkit-animation: bg 15s ease infinite;
          animation: bg 15s ease infinite;
}

@-webkit-keyframes bg {
  0%,
  100% {
    -webkit-filter: hue-rotate(0);
            filter: hue-rotate(0);
  }
  50% {
    -webkit-filter: hue-rotate(360deg);
            filter: hue-rotate(360deg);
  }
}

@keyframes bg {
  0%,
  100% {
    -webkit-filter: hue-rotate(0);
            filter: hue-rotate(0);
  }
  50% {
    -webkit-filter: hue-rotate(360deg);
            filter: hue-rotate(360deg);
  }
}

.navbar-brand img {
  max-width: 15rem;
  -webkit-filter: invert(1);
          filter: invert(1);
}

.header {
  height: 81vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.header h1,
.header p {
  -webkit-animation: header 1s ease forwards;
          animation: header 1s ease forwards;
}

.header h1 {
  position: relative;
}

.header h1::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 1px;
  background: #111;
  -webkit-animation: line 1s ease 1.5s forwards;
          animation: line 1s ease 1.5s forwards;
}

@-webkit-keyframes line {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}

@keyframes line {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}

@-webkit-keyframes header {
  0% {
    -webkit-transform: translateY(5rem) scale(0.6);
            transform: translateY(5rem) scale(0.6);
  }
  100% {
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
  }
}

@keyframes header {
  0% {
    -webkit-transform: translateY(5rem) scale(0.6);
            transform: translateY(5rem) scale(0.6);
  }
  100% {
    -webkit-transform: translateY(0) scale(1);
            transform: translateY(0) scale(1);
  }
}
.main_div {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  min-height: 100vh;
}
/* posts =================================== */
h1:not(:nth-child(1)),
#about h1,
.admin h1 {
  border-bottom: 1px solid #000;
  padding-bottom: 0.2rem;
  text-align: center;
}
.admin h2{
  text-align: center;
}
.posts,
.videos {
  margin: 3rem 0;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  grid-gap: 2rem;
}
.thumb_img {
  position: relative;
}
.thumb_img .content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: rgba(255, 255, 255, 0.5);
  opacity: 0;
  transition: all 0.3s ease;
}
.content a {
  color: #000;
  font-size: 2rem;
  border: 2px solid #000;
  padding: 0.2rem 0.5rem;
  border-radius: 0.3rem;
  -webkit-transform: translateY(20%);
          transform: translateY(20%);
  opacity: 0;
  transition: all 0.3s ease;
}
.thumb_img:hover .content a {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.thumb_img:hover .content {
  opacity: 1;
}

.post img,
.videos img {
  width: 100%;
}

.post .details,
.videos .details {
  padding: 1rem;
}

#about a {
  font-size: 0.96rem;
}

footer {
  background: #f1f1f1;
}
footer p {
  padding: 1rem;
  margin: 0;
}

@media (max-width: 768px) {
  .header {
    height: 87.9vh;
  }
  /* posts =========================== */
  .posts,
  .videos {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
  .navbar-brand img {
    max-width: 11rem;
  }
}

